:root {
  font-size: 16px;
}

body {
  width: 100vw;
  font-family: 'Nunito Sans', sans-serif;
  color: #111;
  letter-spacing: 0.06rem;
  line-height: 1.8rem;
  margin: 0;
}

a {
  text-decoration: none;
  width: 100%;
}

.App {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  color: #575757;
  padding: 5rem 12% 5rem 12%;
}

.dropdown-content {
  height: 0;
  transition: all 0.7s;
  overflow: hidden;
  margin: 1rem 0 0 0;
  border-bottom: none;
}

.contact-link:first-of-type {
  align-items: flex-start;
}

.contact-link:last-of-type {
  align-items: flex-end;
}

.core {
  width: 60%;
}

.core-imgs {
  max-width: 100% !important;
  padding-bottom: 3rem !important;
}

.gighub {
  width: 80%;
}

.github-work-img {
  margin-top: 4px;
  width: 35px;
  height: 35px;
}

.ownr {
  width: 70%;
}

.ownr-imgs {
  max-width: 100% !important;
}

.personal-imgs {
  max-width: 85% !important;
}

.project:last-of-type {
  padding-right: 0;
}

.site {
  font-family: 'Nunito', sans-serif;
  font-size: 3rem;
  font-weight: 900;
}

.light-grey {
  color: #999;
}

.light-black {
  color: #222;
}

.dark-black {
  color: #111;
}

.tile {
  position: relative;
  min-height: 12rem;
  width: 30vw;
  height: 30vw;
  margin-bottom: 5rem;
  object-fit: contain;
  transition: all 0.7s;
  opacity: 1;
}

.tile:hover {
  opacity: 0.4;
}

.tile-empty {
  width: 30%;
  height: 0;
}

.bb-light-grey {
  border-bottom: 1px solid #999;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-wrap {
  flex-wrap: wrap;
}

.contact-img {
  max-width: 50%;
}

.small-img {
  max-width: 27%;
}

.medium-img {
  max-width: 55%;
}

.ml-img {
  max-width: 70%;
}

.flip-img {
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.heading-2 {
  font-size: 2.5rem;
  font-weight: 900;
  color: #222;
}

.heading-3 {
  font-size: 2.5rem;
  font-weight: 400;
  color: #222;
}

.heading-5 {
  line-height: 2.8rem;
  font-size: 2.5rem;
  font-weight: 200;
  color: #222;
}

.heading-6 {
  font-size: 1.5rem;
  font-weight: 200;
  color: #222;
}

.items-baseline {
  align-items: baseline;
}

.items-center {
  align-items: center;
}

.justify-around {
  justify-content: space-around;
}

.justify-between {
  justify-content: space-between;
}

.justify-center {
  justify-content: center;
}

justify-start {
  justify-content: flex-start;
}

.pointer {
  cursor: pointer;
}

.nav a {
  color: #999;
  padding-left: 1rem;
}

.nav a:active,
.nav a:hover {
  color: #111;
}

.paragraph-2 {
  font-size: 2rem;
  font-weight: 200;
  letter-spacing: 0.03rem;
}

.paragraph-3 {
  font-size: 1rem;
  font-weight: 300;
  letter-spacing: 0.03rem;
}

.self-start {
  align-self: start;
}

.self-center {
  align-self: center;
}

.text-center {
  text-align: center;
}

.text-end {
  text-align: end;
}

.outline-0 {
  outline: 0;
}

.bg-graphite {
  background-color: #222;
}
.h100 {
  height: 100%;
}

.minw65 {
  min-width: 65%;
}

.w100 {
  width: 100%;
}

.mw30 {
  max-width: 30%;
}

.mw100 {
  max-width: 100%;
}

.mb6 {
  margin-bottom: 6rem;
}

.pb2 {
  padding-bottom: 2rem;
}

.pb3 {
  padding-bottom: 3rem;
}

.pb4 {
  padding-bottom: 4rem;
}

.pb6 {
  padding-bottom: 6rem;
}

.pb8 {
  padding-bottom: 8rem;
}

.ph0_2 {
  padding-left: 0.2rem;
  padding-right: 0.2rem;
}

.pr4 {
  padding-right: 4rem;
}

.pt2 {
  padding-top: 2rem;
}

.pt3 {
  padding-top: 3rem;
}

.pt4 {
  padding-top: 4rem;
}

.pt5 {
  padding-top: 5rem;
}

.pt6 {
  padding-top: 6rem;
}

.pt7 {
  padding-top: 7rem;
}

.pl1 {
  padding-left: 1rem;
}

.pl2 {
  padding-left: 2rem;
}

.pl3 {
  padding-left: 3rem;
}

.pl8 {
  padding-left: 8rem;
}

.pl7 {
  padding-left: 7rem;
}

.ph3 {
  padding-left: 3rem;
  padding-right: 3rem;
}

.no-pointer-events {
  pointer-events: none;
}

/* ipad pro and smaller */

@media only screen and (max-width: 1024px) {
  .dn-nxl {
    display: none;
  }
  .pl4-nxl {
    padding-left: 4rem;
  }
  .flex-column-nxl {
    display: flex;
    flex-direction: column;
  }
  .self-center-nxl {
    align-self: center;
  }
  .pl0-nxl {
    padding-left: 0;
  }
  .pt4-nxl {
    padding-top: 4rem;
  }
  .pt5-nxl {
    padding-top: 5rem;
  }
  .large-image-nxl {
    max-width: 100%;
  }
  .medium-img-nxl {
    max-width: 55%;
  }
}

/* larger than mobile phone */

@media only screen and (min-width: 30rem) {
  .dn-l {
    display: none;
  }
  .mw30-l {
    max-width: 20%;
  }
}

/* mobile phone */

@media only screen and (max-width: 30rem) {
  .dn-nl {
    display: none;
  }
  .App {
    padding-top: 1rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
  .nav {
    padding-left: 0rem;
    padding-bottom: 0.33rem !important;
    padding-top: 0.33rem !important;
  }
  .nav a {
    padding-left: 0;
  }
  .nav:last-of-type {
    padding-left: 0rem;
    padding-bottom: 0rem;
  }
  .tile {
    width: 47%;
  }
  .large-image-nl {
    max-width: 100%;
  }
  .flex-column-reverse-nl {
    display: flex;
    flex-direction: column-reverse;
  }
  .pt5-nl {
    padding-top: 5rem;
  }
  .pr0-nl {
    padding-right: 0;
  }
  .project:last-of-type {
    padding-bottom: 0;
  }
}
